.product {      /** Div Father */
    font-family: Candara;
    padding: 50px;
}

.product .mainDiv {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.product .productInfo {     /** Div Father com as infos do produto */
    width: 100%;
    background-color: #fff;
    display: grid;
    grid-template-columns: 60% 40%;
    align-items: center;
    justify-content: center;
    padding: 100px 0;
}
.product .line {        /** Linha para separar as divs */
    background-color: #ddd;
    height: 1px;
    width: 100%;
    margin: 30px 0;
}

/** 1 -- Div esquerda */
.product .productInfo .imgDiv {          /** Div Father */
    height: 100%;
    width: 80%;
    border-radius: 5px;
    background-color: #f5f5f5;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 5px solid #fff;
    box-shadow: 1px 1px 5px 1px rgba(0, 0, 0, 0.5);
}
.product .productInfo .imgDiv .img {     /** Imagem em PNG */
    height: 400px;
    width: 400px;
}
/** -- 1 -- */

/** 2 -- Div direita */
.product .productInfo .rightDiv {        /** Div Father */
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
}
.product .productInfo .rightDiv .name {       /** Nome do produto */
    font-size: 25px;
    font-weight: bold;
}

/** 2.1 -- Div de cima com algumas infos do produto */
.product .productInfo .rightDiv .top {       /** Div Father */
    margin-top: 10px;
}
.product .productInfo .rightDiv .top .infoDiv {          /** Div com 2 textos alinhados na horizontal */
    display: flex;
    justify-content: space-between;
    width: 300px;
}
.product .productInfo .rightDiv .top .infoDiv span {         /** Textos da infoDiv */
    font-size: 18px;
    margin-top: 5px;
}
.product .productInfo .rightDiv .top .infoDiv .rightSpan {       /* Apenas os textos da direita da infoDiv */
    width: 100px;
}
/** -- 2.1 -- */

/** 2.2 -- Div de Texto com preço  */
.product .productInfo .rightDiv .divPrice {        /** Div Father */
    display: flex;
    flex-direction: column;
}
.product .productInfo .rightDiv .divPrice .price {       /** Texto */
    font-size: 35px;
    font-weight: bold;
}
/** -- 2.2 -- */

/** 2.3 -- Div com quantidade */
.product .productInfo .rightDiv .qtdDiv {         /** Div Father */
    display: flex;
    align-items: center;
}
.product .productInfo .rightDiv .qtdDiv .btns {      /** Div alinhada horizontalmente com botões */
    display: flex;
    margin-left: 50px;
}
.product .productInfo .rightDiv .qtdDiv .btns .less {         /** Botão de Menos */
    border: 1px solid #ddd;
    width: 50px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}
.product .productInfo .rightDiv .qtdDiv .btns .input {       /** Input entre os botões */
    width: 40px;
    border: 1px solid #ddd;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: 18px;
}
.product .productInfo .rightDiv .qtdDiv .btns .more {        /** Botão de Mais */
    border: 1px solid #ddd;
    width: 50px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}
/** -- 2.3 -- */

/** 2.4 -- Div de baixo com os botões de escolher o que fazer com o produto */
.product .productInfo .rightDiv .bottomDiv {     /** Div Father */
    display: flex;
}
.product .productInfo .rightDiv .bottomDiv .largeBtn {       /** Botão grande (Add To Cart) */
    background-color: #CD553F;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    width: 150px;
    border-radius: 5px;
    font-size: 18px;
    transition: 250ms;
}
.product .productInfo .rightDiv .bottomDiv .smallBtn {       /** Botão pequeno (Wish e Compare) */
    width: 50px;
    height: 50px;
    border-radius: 5px;
    background-color: #CD553F;
    margin-left: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 250ms;
}
/** -- 2.4 -- */

/** 2.5 -- Div com infos de Review abaixo dos botões de (Add cart, Favorite e Compare) */
.product .productInfo .reviewRow {    /** Div Father */
    display: flex;
}
.product .productInfo .reviewRow .reviewRowDiv {     /** Div Item */
    margin-left: 15px;
    font-size: 22px;
    font-family: Cambria;
}
.product .productInfo .reviewRow .reviewRow span {       /** Texto */
    color: #000;
    transition: 250ms;
}
.product .productInfo .reviewRow .lineVertical {     /** Linha vertical que separa as divs */
    height: 100%;
    width: 1px;
    background-color: #000;
    margin-left: 15px;
}
/** -- 2.5 -- */

/** -- 2 -- */

/** 3 -- Divs com infos e para fazer Review do produto */
/** 3.1 -- Styles que se repetem nas duas Divs */
.product .review {      /** Div Father */
    width: 100%;
    border: 1px solid #ddd;
    margin-bottom: 50px;
}
.product .review .top {     /** Essa div não usa nenhum css antes do .top pois serve pra dus divs Fathers diferentes */
    display: flex;
    align-items: center;
    height: 50px;
    border-bottom: 1px solid #ddd;
}
.product .review .top .btnChange { /** Essa div não usa nenhum css antes do .top pois serve pra dus divs Fathers diferentes */
    margin-left: 20px;
    height: 50px;
    display: flex;
    align-items: center;
    border-bottom: 3px solid #CD553F;
    cursor: pointer;
    transition: 250ms;
    font-size: 18px;
}
/* -- 3.1 -- **/


/** 3.2 -- Descrição do produto */
.product .review .productFeatures .descriptionDiv {     /** Div Father */
    margin: 20px;
}
.product .review .productFeatures .descriptionDiv .description {        /** Texto principal (a descrição) */
    font-size: 20px;
    line-height: 1.3;
}
.product .review .productFeatures .featuresDiv {        /** Div com um array de features do produto (características) */
    margin: 20px;
}
.product .review .productFeatures .featuresDiv .title {     /** Título (Features) */
    font-size: 22px;
    font-weight: bold;
}
.product .review .productFeatures .featuresDiv .featuresText {      /** Item do array, mostrando cada feature */
    display: flex;
    flex-direction: column;
    margin: 10px 0 0 50px;
}
.product .review .productFeatures .featuresDiv .featuresText .featuresRow {     /** Bola background preta e texto alinhadas horizontalmente */
    display: flex;
    align-items: center;
    margin-top: 5px;
}
.product .review .productFeatures .featuresDiv .featuresText .featuresRow .smallBall {      /** Bola com background preto */
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background-color: #000;
    margin-right: 10px;
}
/** -- 3.2 -- */

/** 3.3 -- Form para realizar um review */
.product .review .reviewForm .divTop {      /** Div do topo com um texto informando que não há Reviews */
    border: 1px solid #ddd;
    margin: 20px;
    height: 40px;
    display: flex;
    align-items: center;
    padding-left: 10px;
    font-size: 18px;
}
.product .review .reviewForm .title {       /** Título (Write a Review) */
    font-size: 25px;
    font-weight: bold;
    margin: 20px;
}
.product .review .reviewForm .divInput {        /** Div com label e input */
    display: flex;
    flex-direction: column;
    margin: 20px;
}
.product .review .reviewForm .divInput .label {     /** Label */
    font-size: 18px;
    display: flex;
    align-items: center;
    transition: 250ms;
}
.product .review .reviewForm .divInput .input, .product .review .reviewForm .divInput .bigInput {     /** Input */
    border: 1px solid #ddd;
    height: 40px;
    margin-top: 10px;
    font-size: 18px;
    padding-left: 10px;
    transition: 250ms;
}
.product .review .reviewForm .divInput .bigInput {      /** Textarea */
    padding: 10px;
    height: 150px;
}
.product .review .reviewForm .rating {      /** Div com as estrelas para dar uma nota ao produto */
    display: flex;
    align-items: center;
    height: 60px;
    margin-left: 20px;
}
.product .review .reviewForm .rating span {     /** Texto (Rating */
    margin-right: 20px;
    font-size: 18px;
}
.product .review .reviewForm .rating div {      /** Div de cada estrela */
    cursor: pointer;
    margin-right: 5px;
}
/** -- 3.3 -- */

/** -- 3 -- */

/** Hovers */
.product .productInfo .rightDiv .bottomDiv .largeBtn:hover, .product .rightDiv .bottomDiv .smallBtn:hover {     /** Botões grande e pequeno ficam pretos */
    background-color: #000;
    cursor: pointer;
}
.product .productInfo .reviewRow .reviewRowDiv span:hover {     /** Texto de Review ficar #CD553F */
    color: #CD553F;
    cursor: pointer;
}
.product .review .productFeatures .top .btnChange:hover {
    color: #CD553F;
}
/** **/


/** Focus **/
.product .review .reviewForm .divInput .input:focus,
.product .review .reviewForm .divInput .bigInput:focus {      /** Muda a cor da borda do input ao focar */
    outline: 0;
    border: 1px solid #CD553F;
}
/** **/